.item-wrapper {
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    width: 22%;
    position: relative;

    .item-image {
        display: flex;
        height: 95%;
        width: 100%;
        background-size: center;
        background-position: center;
        margin-bottom: 5px;
    }

    .item-footer {
        display: flex;
        height: 5%;
        justify-content: space-between;
        font-size: 18px;
        width: 100%;
        
        .item-name {
            width: 90%;
            margin-bottom: 15px;
        }

        .item-price {
            width: 10%;
        }
    }

    .custom-button{
        position: absolute;
        bottom: 40px;
        width: 80%;
        display: none;
    }

    &:hover{
        .item-image {
            opacity: .8;
        }

        .custom-button{
            display: flex;
            opacity: .85;
        }
    }
}



